import React from 'react'
import styled from 'styled-components'


function Login() {
    return (
        <Container>
            <CTA>
                <CTALogoOne src='/static/images/cta-logo-one.svg' />
                <SignUp>
                    GET ALL THERE
                </SignUp>
                <Description>
                    The irony is that there exists an O(n) sorting algorithm called bucket sort but it has an insane memory trade off needed to achieve o(n), where you need as much memory as the distance between the smallest and the largest value in your list.
                </Description>

                <CTALogoTwo src='/static/images/cta-logo-two.png' />
            </CTA>
        </Container>
    )
}

export default Login

const Description = styled.p`
font-size:11px;
letter-spacing:1.5px;
text-align:center;
line-height:1.5;
`

const SignUp = styled.a`
  width:100%;
  background-color:#0063e5;
  font-weight:bold;
  padding: 17px 0;
  color:#f9f9f9;
  border-radius:4px;
  text-align:center;
  font-size:18px;
  cursor:pointer;
  letter-spacing:1.5px;
  margin-top:8px;
  margin-bottom:12px;
  &:hover{
      background:#0483ee;
      transition: all 250ms;

  }
`

const CTA = styled.div`
max-width:650px;
padding 80px 40px;
width:90%;   
display:flex;
flex-direction:column;
margin-top:100px;
align-items:center;
`

const CTALogoOne = styled.img``

const CTALogoTwo = styled.img`
width:90%;
`

const Container = styled.div`
position:relative;
height:calc(100vh - 70px);
display:flex;
align-items:top;
justify-content:center;



&:before{
    background:url("/static/images/login-background.jpg");
    content:'';
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    opacity:0.7;
    z-index:-1;
    background-position:top;
    background-size:cover;
    background-repeat:no repeat;
}


`
