import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router'
import styled from 'styled-components'

import axios from 'axios'


function Detail() {

    const [movie, setMovie] = useState()

    const { id } = useParams();

    useEffect(() => {
        axios.post('/api/getmovie/', {
            id
        }).then(res => {
            setMovie(res.data)
        })
    }, [])


    return (
        <Container>
            {movie &&
                <>
                    <Background>
                        <img src={movie.backgroundImage} />
                    </Background>
                    <ImageTitle>
                        <img src='/static/images/slider-scale.jpg' />
                    </ImageTitle>
                    <Controls>
                        <PlayButton>
                            <img src='/static/images/play-icon-black.png' />
                            <span>Play</span>
                        </PlayButton>
                        <TrailerButton>
                            <img src='/static/images/play-icon-white.png' />
                            <span>Trailer</span>
                        </TrailerButton>

                        <AddButton>
                            <span>+</span>
                        </AddButton>
                        <GroupWatchButton>
                            <img src='/static/images/group-icon.png' />
                        </GroupWatchButton>
                    </Controls>

                    <SubTitle>
                        {movie.subTitle}
                    </SubTitle>

                    <Description>
                        {movie.description}
                    </Description>
                </>
            }

        </Container>
    )
}

export default Detail

const SubTitle = styled.div`
color:rgb(249, 249, 249);
font-size:15px;
min-height:20px;
margin-top:26px;
`

const Description = styled.div`
line-height:1.4;
font-size:20px;
margin-top:16px;
color:rgb(249, 249, 249);
max-width:260px;
`

const AddButton = styled.button`
width:44px;
height:44px;
display:flex;
align-items:center;
justify-content:center;
border-radius:50%;
border:white;
background-color:rgb(0,0,0,0.6);
color:white;
cursor:pointer;
margin-right:16px;
span{
    font-size:30px;
}
`


const PlayButton = styled.button`
border-radius:4px;
font-size:15px;
display:flex;
align-items:center;
height: 56px;
background-color:rgb(249,249,249);
padding: 0px 24px;
border:none;
letter-spacing:1.8px;
cursor:pointer;
margin-right:22px;  
text-transform:uppercase; 

&:hover{
    background:rgb(198, 198, 198);
}
`

const TrailerButton = styled(PlayButton)`
background:rgba(0,0,0,0.3);
border: 1px solid rgb(249,249,249);   
color: rgb(249,249,249);  
`
const GroupWatchButton = styled(AddButton)`
background:rgb(0,0,0)
`
const Controls = styled.div`
display:flex;
align-items:center;
`

const Container = styled.div`
min-height: calc(100vh - 70px);
padding: 0 calc(3.5vw + 5px);
position: relative;
`

const Background = styled.div`
position: fixed;
top: 0;
left: 0;
bottom: 0;
right: 0;
z-index: -1;
opacity: 0.8;
img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}
`
const ImageTitle = styled.div`
height: 30vh;
width: 30vw;
min-height: 170px;
min-width: 200px;
margin-top:40px;
img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}
`
